/* eslint-disable */
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import store from './store'
import * as fb from 'firebase'
import BuyModalComponent from '@/components/Shared/BuyModal'

Vue.use(Vuetify)
Vue.component('app-buy-modal', BuyModalComponent)

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
      fb.initializeApp({
        apiKey: 'AIzaSyDiUYT6ozBfFzbs8VrSJwgk9wjWaNmNWj8',
        authDomain: 'ads-project-100d7.firebaseapp.com',
        databaseURL: 'https://ads-project-100d7.firebaseio.com',
        projectId: 'ads-project-100d7',
        storageBucket: 'ads-project-100d7.appspot.com',
        messagingSenderId: '1099173147887'
      })

    fb.auth().onAuthStateChanged(user => {
      if (user) {
        this.$store.dispatch('autoLoginUser', user)
      }
    })

    this.$store.dispatch('fetchAds')
  }
})



